import React, { memo, useEffect } from "react";
import classNames from "classnames";
import { shallowEqual, useDispatch, useSelector } from "react-redux";

import withWindowSize from "@/components/higher-order/withWindowSize";
import {
  HomeContentEventLoadMoreWrapper,
  HomeContentLayoutModeWrapper,
  HomeContentWrapper,
} from "./style";
import KTEvemtsListView from "@/components/events-list-view";
import {
  getChangeHomeLayoutModeAction,
  getHomeEventsAction,
} from "../../../../store/actionCreators";
import { LAYOUT_MODE_CARD, LAYOUT_MODE_LIST } from "@/common/constants";
import { cardModeSvg, listModeSvg } from "@/common/icon-svg";

const KTHomeContent = memo(({ windowSize }) => {
  const { layoutMode, events = [] } = useSelector(
    (state) => ({
      layoutMode: state.getIn(["home", "layoutMode"]),
      events: state.getIn(["home", "events"]),
    }),
    shallowEqual
  );

  const dispatch = useDispatch();
  useEffect(() => {
    if (events.length === 0) {
      dispatch(getHomeEventsAction());
    }
  }, [dispatch, events.length]);

  const onclickChangeLayoutMode = (mode) => {
    dispatch(getChangeHomeLayoutModeAction(mode));
  };

  const onclickLoadMoreButton = () => {
    dispatch(getHomeEventsAction());
  };

  function calculateSize(windowSize) {
    const windowWidth = windowSize.width;
    if (windowWidth >= 1920) {
      return {
        wrapperPaddingLeftRight: "100px",
        cardWidth: 400,
      };
    } else if (windowWidth <= 1024) {
      return {
        wrapperPaddingLeftRight: "50px",
        cardWidth: 350,
      };
    } else {
      const ratio = (windowWidth - 1024) / (1920 - 1024);

      const wrapperPaddingLeftRight = ratio * 50 + 50;
      const cardWidth = ratio * 50 + 350;

      return {
        wrapperPaddingLeftRight: `${wrapperPaddingLeftRight}px`,
        cardWidth: cardWidth,
      };
    }
  }

  const { wrapperPaddingLeftRight, cardWidth } = calculateSize(windowSize);

  return (
    <HomeContentWrapper style={{ padding: `0 ${wrapperPaddingLeftRight}` }}>
      <HomeContentLayoutModeWrapper>
        {cardModeSvg({
          className: classNames("layoutModel", {
            layoutModelActive: layoutMode === LAYOUT_MODE_CARD,
          }),
          onClick: () => onclickChangeLayoutMode(LAYOUT_MODE_CARD),
        })}
        {listModeSvg({
          className: classNames("layoutModel", {
            layoutModelActive: layoutMode === LAYOUT_MODE_LIST,
          }),
          onClick: () => onclickChangeLayoutMode(LAYOUT_MODE_LIST),
        })}
      </HomeContentLayoutModeWrapper>

      <KTEvemtsListView
        key={layoutMode}
        events={events}
        layoutMode={layoutMode}
        cardWidth={cardWidth}
        windowWidth={windowSize.width}
      />
      <HomeContentEventLoadMoreWrapper>
        <div className="button" onClick={(item) => onclickLoadMoreButton()}>
          Load More
        </div>
      </HomeContentEventLoadMoreWrapper>
    </HomeContentWrapper>
  );
});

export default withWindowSize(KTHomeContent);
